import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { insertUser } from '../../lib/Api';

export default class UserInsert extends React.Component {
    constructor(props) {
        super(props);
        this.handleName = this.handleName.bind(this);
        this.handleInstagram = this.handleInstagram.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePhone = this.handlePhone.bind(this);
        this.handleAddress = this.handleAddress.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.submitUser = this.submitUser.bind(this);
        this.handleType = this.handleType.bind(this);
        this.state = {
            user: {},
            errMsg: '',
            hideAlert: true,
            type: ''
        }
    }

    handleName(event) {
        const name = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                name   
            }
        }));
    }

    handleInstagram(event) {
        const instagramUsername = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                instagramUsername  
            }
        }));
    }

    handleEmail(event) {
        const email = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                email
            }
        }));
    }

    handlePhone(event) {
        const phone = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                phone
            }
        }));
    }

    handleAddress(event) {
        const address = event.target.value;
        this.setState(prevState => ({
            user: {
                ...prevState.user,
                address
            }
        }));
    }

    submitUser() {
        let user = this.state.user;
        if (user.name === '' || user.name === undefined) {
            this.setState({ errMsg: 'Name is required', hideAlert: false }, () => {
                window.setTimeout(()=>{
                  this.setState({ errMsg: '', hideAlert: true })
                }, 4000)
            });
        } else {
            user.type = this.state.type;
            let nameSplitted = user.name.split(' ');
            if (user.email === '' || user.email === undefined) {
                user.email = `${nameSplitted[0]}@${nameSplitted[1]}.com`;
            }
            if (user.instagramUsername === '' || user.instagramUsername === undefined) {
                user.instagramUsername = `__${nameSplitted[0]}__${nameSplitted[1]}__`;
            }
            if (user.phone === undefined || user.phone === '' || user.phone.match(/^\+[0-9]{12,13}$/g) === null) {
                this.setState({ errMsg: 'phone number must start with "+", followed by 12-13 digits', hideAlert: false }, () => {
                    window.setTimeout(()=>{
                      this.setState({ errMsg: '', hideAlert: true })
                    }, 4000)
                });
            } else {
                if (this.state.hideAlert === true) {
                    insertUser(user).then(res => {
                        this.props.getUsers();
                        this.props.closeModal();
                    }, err => {
                        if (typeof err.response.data == 'object') {
                            this.setState({ errMsg: err.response.data.msg, hideAlert: false }, () => {
                                window.setTimeout(()=>{
                                  this.setState({ errMsg: '', hideAlert: true })
                                }, 4000)
                            });
                            
                        } else {
                            this.setState({ errMsg: err.response.data, hideAlert: false }, () => {
                                window.setTimeout(()=>{
                                  this.setState({ errMsg: '', hideAlert: true })
                                }, 4000)
                            });
                        }
                    });
                }
            }
        }
    }

    closeModal() {
        this.setState({ errMsg: '', hideAlert: true });
        this.props.closeModal();
    }

    handleType(e) {
        this.setState({ type: e.target.value });
    }

    render() {
        return(
            <div>
                <Modal isOpen={this.props.showModalInsert}>
                        <ModalHeader>New User</ModalHeader>
                        <ModalBody>
                            <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                                { this.state.errMsg }
                            </div>
                            <form>
                                <div className="form-group">
                                    <div className="form-group">
                                        <label>Name:</label>
                                        <input type="text" className="form-control" id="userName" placeholder="Enter Your Name" onChange={this.handleName} />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="form-group">
                                        <label>Instagram Username:</label>
                                        <input type="text" className="form-control" id="userInstagram" placeholder="Enter Your Instagram Username" onChange={this.handleInstagram} />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="form-group">
                                        <label>Email:</label>
                                        <input type="text" className="form-control" id="userEmail" placeholder="Enter Your Email" onChange={this.handleEmail} />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="form-group">
                                        <label>Phone:</label>
                                        <input type="text" className="form-control" id="userPhone" placeholder="Enter Your Phone" onChange={this.handlePhone} />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="form-group">
                                        <label>Address:</label>
                                        <input type="text" className="form-control" id="userAddress" placeholder="Enter Your Address" onChange={this.handleAddress} />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label>Type:</label>
                                </div>
                                <div className="form-group">
                                    <div className="custom-control custom-radio">
                                        <input type="radio" id="friendRadio" name="customRadio" className="custom-control-input" value="friend" onChange={this.handleType} />
                                        <label className="custom-control-label" htmlFor="friendRadio">Friend</label>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="custom-control custom-radio">
                                        <input type="radio" id="potentialRadio" name="customRadio" className="custom-control-input" value="potential" onChange={this.handleType} />
                                        <label className="custom-control-label" htmlFor="potentialRadio">Potential</label>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="custom-control custom-radio">
                                        <input type="radio" id="importantRadio" name="customRadio" className="custom-control-input" value="important" onChange={this.handleType} />
                                        <label className="custom-control-label" htmlFor="importantRadio">Important</label>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <div className="custom-control custom-radio">
                                        <input type="radio" id="noneRadio" name="customRadio" className="custom-control-input" value="" onChange={this.handleType} />
                                        <label className="custom-control-label" htmlFor="noneRadio">None</label>
                                    </div>
                                </div>
                            </form>
                        </ModalBody>
                        <ModalFooter>
                            <button type="submit" className="btn btn-info" onClick={(event) => this.submitUser(event)}>Add User</button>
                            <button type="button" className="btn btn-danger" onClick={this.closeModal}>Cancel</button>
                        </ModalFooter>
                    </Modal>
            </div>
        )
    }
}