import React from 'react';
import { getRankings } from '../../lib/Api';
import Header from '../Header/Header';
import Loading from '../Loading/Loading';

export default class Revenue extends React.Component {

    constructor(props) {
        super(props);
        this.handleFilter = this.handleFilter.bind(this);
        this.state = {
            rankings: [],
            filter: 'socks',
            loading: false
        }
    }

    componentDidMount() {
        this.setState({ loading: true });
        getRankings().then(res => {
            this.setState({ loading: false });
            this.setState({ rankings: res.data.rankings });
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    handleFilter(e) {
        this.setState({ filter: e.target.value });
        this.setState({ loading: true });
        getRankings(e.target.value).then(res => {
            this.setState({ loading: false });
            this.setState({ rankings: res.data.rankings });
        }).catch(err => {

        })
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="row">
                    <div className="offset-md-4 col-md-4 text-center">
                        <div className="row">
                            <div className="offset-md-3 col-md-6">
                                <div className="form-group">
                                    <select className="custom-select" value={this.state.filter} onChange={this.handleFilter}>
                                        <option value="orders"># of orders</option>
                                        <option value="socks"># of socks</option>
                                    </select>
                                </div>
                            </div>
                        </div>            
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-4 offset-md-4">
                        <div className="text-center">
                            <Loading loading={ this.state.loading } />
                        </div>
                        <table class="table" hidden={ this.state.loading }>
                            <thead class="thead-light">
                                <tr>
                                <th>#</th>
                                <th>USER</th>
                                <th># OF { this.state.filter.toUpperCase() }</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.rankings.map((ranking, index) => {
                                        return <tr>
                                            <td>{ index + 1 }</td>
                                            <td>{ ranking._id.customerId }</td>
                                            <td>{ ranking.count }</td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}