import React from 'react';
import { getRevenue } from '../../lib/Api';
import Header from '../Header/Header';

import './Revenue.css';
import Loading from '../Loading/Loading';

export default class Revenue extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            total: '',
            loading: false
        }
    }

    componentDidMount() {
        this.setState({ loading: true });
        getRevenue().then(res => {
            this.setState({ loading: false });
            this.setState({ total: res.data.result[0].total });
        }).catch(err => {
            console.log('err: ', err);
        });
    }

    render() {
        return(
            <div>
                <Header></Header>
                <div className="row">
                    <div className="col-md-4 offset-md-4 revenue__container">
                        <div className="revenue__content">
                            <Loading loading={ this.state.loading } />
                        </div>
                        <div className="revenue__content" hidden={ this.state.loading }>
                            <b>Revenue</b> <br />
                            { this.state.total? this.state.total.toFixed(0) : '' } L.E.
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}