import React from 'react';
import ClipLoader from "react-spinners/ClipLoader";

const Loading = (props) => {
    return (
        <div className="col-lg-12 center">
            <h2 hidden={ !props.loading } style={{ color: '#17a2b8' }}>Fetching..</h2>
            <ClipLoader
                size={ props.size || 150 }
                color={"#17a2b8"}
                loading={ props.loading }
                />
        </div>
    );
}
 
export default Loading;