import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { insertProduct } from '../../lib/Api';

export default class ProductInsert extends React.Component {
    constructor(props) {
        super(props);
        this.handleName = this.handleName.bind(this);
        this.handleCode = this.handleCode.bind(this);
        this.handlePrice = this.handlePrice.bind(this);
        this.handleQuantity = this.handleQuantity.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.submitProduct = this.submitProduct.bind(this);
        this.handleImage = this.handleImage.bind(this);
        this.state = {
            product: {},
            errMsg: '',
            hideAlert: true,
            image: null
        }
    }

    handleName(event) {
        const name = event.target.value;
        this.setState(prevState => ({
            product: {
                ...prevState.product,
                name   
            }
        }));
    }

    handleCode(event) {
        const code = event.target.value;
        this.setState(prevState => ({
            product: {
                ...prevState.product,
                code  
            }
        }));
    }

    handlePrice(event) {
        const price = event.target.value;
        if (price > 0) {
            this.setState(prevState => ({
                product: {
                    ...prevState.product,
                    price
                }
            }));
        }
    }

    handleQuantity(event) {
        const quantity = event.target.value;
        if (quantity > 0) {
            this.setState(prevState => ({
                product: {
                    ...prevState.product,
                    quantity
                }
            }));
        }
    }

    submitProduct() {
        const data = new FormData();
        data.append('imageName', this.state.file);
        let product = this.state.product;
        if (product.name == null || product.code == null || product.price == null || product.quantity == null) {
            this.setState({ errMsg: 'please fill all fields with valid values', hideAlert: false }, () => {
                window.setTimeout(()=>{
                  this.setState({ errMsg: '', hideAlert: true })
                }, 4000)
            });
        } else {
            data.append('name', product.name);
            data.append('code', product.code);
            data.append('price', product.price);
            data.append('quantity', product.quantity);
            insertProduct(data).then(res => {
                this.props.getProducts();
                this.props.closeModal();
            }, err => {
                if (err.response.data && !err.response.data.msg) {
                    if (err.response.data.includes('Image already exists')) {
                        this.setState({ errMsg: 'The image already exists', hideAlert: false }, () => {
                            window.setTimeout(()=>{
                                this.setState({ errMsg: '', hideAlert: true })
                            }, 7000)
                        });
                    } else {
                        this.setState({ errMsg: 'The code should be in the form of one capital letter, one number, a dot then a series of numbers (A1.1)', hideAlert: false }, () => {
                            window.setTimeout(()=>{
                              this.setState({ errMsg: '', hideAlert: true })
                            }, 7000)
                        });
                    }
                } else {
                    this.setState({ errMsg: err.response.data.msg, hideAlert: false }, () => {
                        window.setTimeout(()=>{
                          this.setState({ errMsg: '', hideAlert: true })
                        }, 4000)
                    });
                }
            });
        }
    }

    closeModal() {
        this.setState({ errMsg: '', hideAlert: true });
        this.props.closeModal();
    }

    handleImage(event) {
        this.setState({ file: event.target.files[0] });
    }

    render() {
        return(
            <div>
                <Modal isOpen={this.props.showModalInsert}>
                        <ModalHeader>New Product</ModalHeader>
                        <ModalBody>
                        <div className="alert alert-danger" role="alert" hidden={this.state.hideAlert}>
                            { this.state.errMsg }
                        </div>
                        <form>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Name:</label>
                                    <input type="text" className="form-control" id="productName" placeholder="Name" onChange={this.handleName} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Code:</label>
                                    <input type="text" className="form-control" id="productCode" placeholder="Code" onChange={this.handleCode} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Price:</label>
                                    <input type="number" className="form-control" id="productPrice" placeholder="Price" onChange={this.handlePrice} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Quantity:</label>
                                    <input type="number" className="form-control" id="productQuantity" placeholder="Quantity" onChange={this.handleQuantity} />
                                </div>
                            </div>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>Image:</label>
                                    <input type="file" name="image" className="form-control" id="productImage" placeholder="Upload an image..." onChange={this.handleImage}/>
                                </div>
                            </div>
                        </form>
                        </ModalBody>
                        <ModalFooter>
                            <button type="submit" className="btn btn-info" onClick={(event) => this.submitProduct(event)}>Add Product</button>
                            <button type="button" className="btn btn-danger" onClick={this.closeModal}>Cancel</button>
                        </ModalFooter>
                    </Modal>
            </div>
        )
    }
}